@import "./variables";
@import "./mixins";

.cart {
  max-width: 1136px;
  margin: 0 auto;
  flex-grow: 1;
  @include onTablet {
    max-width: 592px;
  }
  @include onMobile {
    max-width: 288px;
  }

  &__title {
    font-size: 32px;
    font-weight: 700;
    line-height: normal;
    letter-spacing: -0.32px;
    margin-bottom: 8px;
  }

  &__content {
    display: flex;
    gap: 16px;
    padding-bottom: 64px;
    @include onTablet {
      flex-direction: column;
    }
  }

  &__item {
    display: flex;
    flex-direction: column;
    gap: 35px;
    @include onMobile {
      gap: 16px;
    }
  }

  &__container {
    box-sizing: border-box;
    display: flex;
    align-items: center;
    gap: 20px;
    padding: 24px;
    border: 1px solid $gray-elements;
    @include onMobile {
      gap: 16px;
      flex-wrap: wrap;
      justify-content: space-between;
    }
  }

  &__remove {
    padding: 0;
    margin: 0;
    border: 0;
  }

  &__remove-icon path {
    transition: fill 500ms;
  }

  &__remove:hover .cart__remove-icon path {
    fill: $gray-primary;
  }

  &__photo-container {
    box-sizing: border-box;
    padding: 7px;
  }

  &__photo {
    width: 66px;
    height: 66px;
    object-fit: contain;
  }

  &__name {
    width: 295px;
    color: $gray-primary;
    font-weight: 500;
    @include onMobile {
      width: 102px;
    }
  }

  &__count {
    display: flex;
  }

  &__count-button,
  &__count-value {
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 32px;
    height: 32px;
  }

  &__count-button {
    border: 1px solid $gray-icons-placeholders;
  }

  &__price {
    color: $gray-primary;
    font-size: 22px;
    font-weight: 700;
    line-height: 140%;
  }

  &__fullPrice {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: max-content;
    width: 368px;
    padding: 24px;
    border: 1px solid $gray-elements;
    @include onTablet {
      width: 100%;
    }
  }

  &__fullPrice-count {
    color: $gray-primary;
    font-size: 32px;
    font-weight: 700;
    line-height: normal;
    letter-spacing: -0.32px;
  }

  &__fullPrice-button {
    width: 320px;
    height: 48px;
    padding: 0;
    border: 0;
    background-color: $gray-primary;
    color: #fff;
    text-align: center;
    transition: box-shadow 500ms;
    @include onTablet {
      width: 100%;
    }
  }

  &__fullPrice-button:hover {
    box-shadow: 0 3px 13px 0 rgba(23, 32, 49, 0.26);
  }

  &__message {
    padding-top: 20px;
    text-align: center;
  }
}

.mb-24 {
  margin-bottom: 24px;
}
